



import {Injectable} from "@angular/core";
import {Book} from "./BookService";

@Injectable()
export class CartService {

    private cart: Book[] = [];

    addBook(book) {
      this.cart.push(book);
      this.saveCart();
    }

    removeBook(book) {
      this.cart = this.cart.filter(item => item.id !== book.id);
      this.saveCart();
    }

    getCart() {
      this.loadCart();
      return this.cart;
    }

    saveCart() {
      localStorage.setItem('currentUser', JSON.stringify(this.cart));
    }

    loadCart() {
      let storage = JSON.parse(localStorage.getItem('currentUser'));
      this.cart = storage.map(item => this.parseBook(item));
    }

    parseBook(item) {
      return new Book(item.id, item.title, item.authors, item.description, item.cover);
    }


}



