import { Component, OnInit } from '@angular/core';
import {Book, BookService} from '../services/BookService';
import {Router} from "@angular/router";
import {CartService} from "../services/CartService";

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {

  constructor(private bookService: BookService, private router: Router, private cartService: CartService) { }

  values = '';
  result = false;

  books = [];

  ngOnInit() {
  }

  searchBooks() {
    this.getBooksFromApi(this.values);

  }

  onSelect(book: Book) {
    this.router.navigate(['/book', book.id]);
  }

  getBooksFromApi(query) {
    this.bookService.getBooks(
      query).subscribe(
        results => this.books = results || [],
      () => console.log("onError"),
      () => this.result = this.books.length === 0
        );
  }

  addBookToCart(book) {
    this.cartService.addBook(book);
  }

}



