import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import 'rxjs/add/operator/map';


export class Book {

  constructor(public id: string, public title: string, public authors: string[], public description: string, public cover: string) {}

}

@Injectable()
export class BookService {

  private searchUrl = 'https://www.googleapis.com/books/v1/volumes';
  private oneBookUrl = 'https://www.googleapis.com/books/v1/volumes/';

  constructor(private http: HttpClient) { }

  getBook(id) {
    let apiUrl = this.oneBookUrl + id;

    return this.http
      .get(apiUrl)
      .map(response => this.createBook(response)
      );
  }

  getBooks(query) {
    let apiUrl = this.searchUrl + '?q=' + query.split(" ").join("+");

    console.log(apiUrl);

    return this.http
      .get(apiUrl)
      .map(response => this.extractResult(response["items"])
      );
  }

  extractResult(result) {
    return result && result.map(this.createBook);
  }

  createBook(item) {

    let volumeInfo = item.volumeInfo;

    let imageLink = volumeInfo.imageLinks && volumeInfo.imageLinks.thumbnail;

    return new Book(item.id, volumeInfo.title, volumeInfo.authors, volumeInfo.description, imageLink);
  }

}



